import React, { Component } from 'react';
import './orders.css';
import Order from './order';
import axios from 'axios';
import { connect } from 'react-redux';
import { Formik } from 'formik';
import * as Yup from 'yup';
import Error from '../checkout/error';
import { authProcess } from '../../actions/auth';

class Orders extends Component {
    state = {
        orders: [],
        loading: true
    }

    componentDidMount() {
        axios.get('https://coconut-oil-landingpage.firebaseio.com//orders.json')
        .then(response => {
            const data = Object.values(response.data);
            console.log(data);
            this.setState({orders: data, loading: false});
        })
        .catch((error) => console.log(error));
    }

    buttonHandler = (orderData) => {
        console.log(orderData);
        this.props.onAuth(orderData.email, orderData.phone);
    }

    render() {
        const {token, userId} = this.props;
        console.log(token, userId);
        const validationSchema = Yup.object().shape({
            phone: Yup.string()
                .min(2, "Must have at least 2 characters")
                .max(30, "Must be shorter than 30")
                .required("Must enter a phone"),           
            email: Yup.string()
                .email("Must be a valid email address")
                .max(30, "Must be shorter than 30")
                .required("Must enter an email")            
        });
        
        return(
            <div className="container">
                {token
                    ?   <div className="row m-5">
                            {this.state.orders.map((order, id) => (
                            <Order 
                                key={id}
                                price = {order.price.totalItems}
                                count = {order.totalNumber.totalPrice}
                                customer = {order.customer}
                                products = {order.orderedProduct}
                            />
                            ))}
                        </div>
                    :   <div className="row my-5 border bg-light rounded">
                        <Formik 
                            initialValues={{email: "", phone: ""}}
                            validationSchema={validationSchema}
                            onSubmit = {(values, {setSubmitting, resetForm})=>{
                                this.buttonHandler(values);
                                resetForm();
                            }}
                        >
                            {({
                                values, 
                                errors, 
                                touched, 
                                handleChange, 
                                handleBlur, 
                                handleSubmit, 
                                isSubmitting
                            })=>(
                                <form onSubmit={handleSubmit} className="order__fields col-8 my-4">
                                    <div className="form-row">
                                        <div className="form-group col-md-6">
                                            <label htmlFor="email">Email</label>
                                            <input  
                                                onChange={ handleChange } 
                                                onBlur = {handleBlur}
                                                value = {values.email}
                                                type="email" 
                                                className="form-control"
                                                name="email" 
                                                id="email"
                                            />
                                            <Error touched={touched.email} message={errors.email}/>
                                        </div>
                                        <div className="form-group col-md-6">
                                            <label htmlFor="phone">Телефон</label>
                                            <input  
                                                onChange={ handleChange }
                                                onBlur = {handleBlur}
                                                value = {values.phone} 
                                                type="text" 
                                                className="form-control"
                                                name="phone"  
                                                id="phone"
                                            />
                                            <Error touched={touched.email} message={errors.email}/>
                                        </div>
                                    </div>
                                    <button disabled = {isSubmitting} className="btn btn-lg mt-3 btn-block btn-primary" type="submit">Sign in</button>
                                </form>
                                )
                            }
                        </Formik>
                </div>
            }
            </div>
        );
        
    };
}

const mapStateToProps = ({auth}) => {
    return {
        token: auth.token,
        userId: auth.userId
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        onAuth: (email, password) => dispatch(authProcess(email, password))
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Orders);