import React from 'react';
import './basket.css';
import { Link } from 'react-router-dom';
import Header from '../header';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as cartActions from '../../actions/cart';

const Basket = ({orderedItems, isMenuShow, 
                onDelete, onAddToCart, 
                onDecrease, totalItems, 
                totalPrice, onCloseModal}) => {
    const renderRow = (item, idx) => {
        const {id, title, value, price, count, total} = item;
        return(
            <div key={id} className="row align-items-center p-1 my-2">
                <div className="col-5 col-sm-6 col-md-4 col-lg-3">
                    <div>{idx +1}. {title}
                        <span>({value})</span>
                    </div>
                </div>
                <div className="col-7 col-sm-6 col-md-4 col-lg-3">
                    <a onClick={()=>onAddToCart(item)}>
                        <i className="fa fa-plus-circle"/>
                    </a>
                    <span className="border rounded mx-2 px-2">{count}</span>
                    <a onClick={()=>onDecrease(item)}>
                        <i className="fa fa-minus-circle"/>
                    </a>
                    <span className="mx-3">{total} &#8372;</span>
                                        {/*
                                        <button
                                            onClick={()=>onDecrease(item)}  
                                            className="btn btn-outline-warning btn-sm ml-2">
                                            <i className="fa fa-minus-circle"/>
                                        </button>*/}
                    <a className="ml-sm-3"
                        onClick={()=>onDelete(item)}>
                        <i className="fa fa-trash-o"/>
                    </a>
                </div>
            </div>
        );
    };
    {/*const renderRow = (item, idx) => {
        const {id, title, value, price, count, total} = item;
        return(
            <tr key={id}>
                <td>{idx + 1}</td>
                <td>{title}</td>
                <td>{value}</td>
                <td>{price}</td>
                <td>{count}</td>
                <td>{total}</td>
                <td>
                    <button
                        onClick={()=>onAddToCart(item)} 
                        className="btn btn-outline-success btn-sm">
                        <i className="fa fa-plus-circle"/>
                    </button>
                    <button
                        onClick={()=>onDecrease(item)}  
                        className="btn btn-outline-warning btn-sm ml-2">
                        <i className="fa fa-minus-circle"/>
                    </button>
                    <button
                        onClick={()=>onDelete(item)}   
                        className="btn btn-outline-danger btn-sm ml-2">
                        <i className="fa fa-trash-o"/>
                    </button>
                </td>
            </tr>
        );
    };
    */}
    return(
        <div className="container">
            <Header isMenuShow={!isMenuShow}/>
            <h3>Ваш заказ</h3>
            <div className="my-4">
                { orderedItems.map(renderRow) }
            </div>
            <div className="cart__content mt-3">
                <div className="d-flex flex-column">
                    <div className="d-flex">
                        <h5>Количество: ({totalPrice})</h5>
                        <h5 className="ml-3">Общая сумма: {totalItems}</h5>
                    </div>
                    <div className="my-3">
                        <button className="btn btn-danger w-50">
                            <Link onClick={()=> onCloseModal()} to="/checkout">Оформить заказ</Link>
                        </button>
                    </div>  
                </div>
            </div>
        </div>
    );
};

const mapStateToProps = ({ cart, menu}) => ({
    totalPrice: cart.cartItems.reduce((total, book) => total + book.count, 0),
    orderedItems: cart.cartItems,
    totalItems: cart.orderTotal,
    isMenuShow: menu.isMenuShow
});

const mapDispatchToProps = dispatch => ({
    ...bindActionCreators(cartActions, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(Basket);