import React from 'react';
import './modal.css';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as cartActions from '../../actions/cart';
import { Link } from 'react-router-dom';

const Modal = ({orderedItems, isClicked, totalItems, totalPrice, onDelete, onAddToCart, onDecrease, onCloseModal}) => {
    const renderRow = (item, idx) => {
        const {id, title, value, price, count, total} = item;
        return(
            <div key={id} className="row align-items-center border-bottom p-1 my-2">
                <div className="col-5 col-sm-6">
                    <div>{idx +1}. {title}
                         <span>({value})</span>
                    </div>
                </div>
                <div className="col-7 col-sm-6">
                    <a onClick={()=>onAddToCart(item)}>
                        <i className="fa fa-plus-circle"/>
                    </a>
                    <span className="border rounded mx-2 px-2">{count}</span>
                    <a onClick={()=>onDecrease(item)}>
                        <i className="fa fa-minus-circle"/>
                    </a>
                    <span className="mx-3">{total} &#8372;</span>
                    {/*
                    <button
                        onClick={()=>onDecrease(item)}  
                        className="btn btn-outline-warning btn-sm ml-2">
                        <i className="fa fa-minus-circle"/>
                    </button>*/}
                    <a className="ml-sm-3"
                        onClick={()=>onDelete(item)}>
                        <i className="fa fa-trash-o"/>
                    </a>
                </div>
            </div>
        );
    };
    return (
            <div 
                className = "modal_style"
                style = {{
                    transform: isClicked ? 'translateY(0)' : 'translateY(-100vh)',
                    opacity: isClicked > 0 ? '1' : '0'
                }}
            >
                <div className="container bg-light border rounded border-dark">
                    <div className="d-flex flex-column py-3">
                        <div>
                            <div className="d-flex justify-content-between">
                                <div>
                                    <h5>Вы добавили в корзину</h5>
                                </div>
                                {/*
                                <button onClick={() => onCloseModal()} type="button" className="btn btn-outline-secondary btn-sm">
                                    Закрыть корзину
                                </button>*/}
                                <a onClick= {()=> onCloseModal()} className="ml-2">
                                    <i class="fa fa-times-circle fa-3x"/>
                                </a>
                            </div>
                            <div className="mt-3">
                                { orderedItems.map(renderRow) }
                            </div>
                            <div className="cart__content mt-3">
                                <div className="d-flex justify-content-left">
                                    <h6>Количество ({totalPrice})</h6>
                                    <h6 className="ml-3">Общая сумма: {totalItems} &#8372;</h6>
                                </div>
                                <div className="text-center">
                                    <button className="btn btn-danger w-50 my-3">
                                            <Link onClick={()=> onCloseModal()}  to="/checkout">Оформить заказ</Link>
                                    </button> 
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    );
}

const mapStateToProps = ( {cart} ) => ({
    totalItems: cart.orderTotal,
    totalPrice: cart.cartItems.reduce((total, book) => total + book.count, 0),
    orderedItems: cart.cartItems,
    isClicked: cart.isClicked
});

const mapDispatchToProps = dispatch => ({
    ...bindActionCreators(cartActions, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(Modal);