import React from 'react';
import './product.css';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as cartActions from '../../../actions/cart';

const Product = (product) => {
        const {title, price, image, value, onAddToCart} = product;
    return (
        <div className="col-12 col-lg-4 row mt-3 
                        product__holder rounded
                        p-3 text-left align-items-center">
            <div className="col-6">
                <img src= { image } className="img-fluid product__image" />
            </div>
            <div className="product__data col-6">
                <p className="product__title">
                    { title }
                </p>
                <div className="product__value">
                    Объём: { value } мл.
                </div>
                <div className="product__price">
                    Цена:  <span className="font-weight-bold">{ price } грн.</span>
                </div>
                <button className="mt-3 btn btn-danger" onClick={()=> onAddToCart(product)}>Заказать</button>
            </div>
        </div>
        );
}

const mapStateToProps = ({cart}) => ({
    totalPrice: cart.cartItems,
    count: cart.orderTotal
});

const mapDispatchToProps = dispatch => ({
...bindActionCreators(cartActions, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(Product);