const initialState = {
    isMenuClicked: false,
    isMenuShow: true
};
 
export default (state = initialState, action) => {
    switch (action.type) {       
        case 'ON_MENU_OPEN':
            return {
                ...state,
                isMenuClicked: true
            };
        case 'ON_MENU_CLOSE':
            return {
                ...state,
                isMenuClicked: false
            };
        default:
          return state;
    }
}