import React from 'react';
import './property.css';
import check from './../check.jpg';

const Property = (props) => {
    return (
        <div className="d-flex align-items-center bg-light p-3">
            {/*
            <div className="property__image text-center w-25">
                <img src={check} className="img-fluid" />
            </div>
            */}
            <p className="property__data ml-3 w-75">
                {props.data}
            </p>
        </div>
    );
}

export default Property;