import React from 'react';
import './menubar.css';
import { Link, animateScroll as scroll } from 'react-scroll';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as menuActions from '../../actions/menu';

const Menubar = ({isClicked, onMenuClose}) => {
    if(isClicked) {
        return (
            <div className="menubar row align-items-top">
                <nav className="col-8 mt-3">
                    <div>
                        <Link onClick= {()=> onMenuClose()} activeClass="active"
                        to="section1"
                        spy={true}
                        smooth={true}
                        offset={-10}
                        duration= {500}>
                        О масле
                        </Link>
                    </div>
                    <div className="mt-1">
                        <Link onClick= {()=> onMenuClose()} activeClass="active"
                        to="section2"
                        spy={true}
                        smooth={true}
                        offset={-10}
                        duration= {500}>
                        Каталог
                        </Link>
                    </div>
                    <div className="mt-1">
                        <Link onClick= {()=> onMenuClose()} activeClass="active"
                        to="section3"
                        spy={true}
                        smooth={true}
                        offset={-10}
                        duration= {500}>
                        Как применять
                        </Link>
                    </div>
                    <div className="mt-1">
                        <Link onClick= {()=> onMenuClose()} activeClass="active"
                        to="section4"
                        spy={true}
                        smooth={true}
                        offset={-10}
                        duration= {500}>
                        Как заказать
                        </Link>
                    </div>
                    <div className="mt-1">
                        <Link onClick= {()=> onMenuClose()} activeClass="active"
                        to="section5"
                        spy={true}
                        smooth={true}
                        offset={-10}
                        duration= {500}>
                        Вопросы и ответы
                        </Link>
                    </div>
                </nav>
                <div className="col-4">
                    <a onClick= {()=> onMenuClose()} className="ml-2">
                        <i class="fa fa-times-circle fa-3x"/>
                    </a>
                </div>
            </div>
        );
        }
    else {
        return null;
    }
}

const mapStateToProps = ({ menu }) => ({
    isClicked: menu.isMenuClicked
});

const mapDispatchToProps = dispatch => ({
    ...bindActionCreators(menuActions, dispatch)
});


export default connect(mapStateToProps, mapDispatchToProps)(Menubar);