import React from 'react';
import './questions.css';
import Question from './question';

const Questions = () => {
    return (
        <div>
            <h2 className="questions__heading text-center">Вопросы</h2>
            <div className="mt-4">
                <div className="questions__content text-center" id="section5">
                    <Question />
                </div>
            </div>
        </div>
    );
}

export default Questions;