import React from 'react';
import Menubar from '../menubar';
import './topbar.css';

const Topbar = () => {
    return (
        <div className="bg-dark">
            <div className="container">
                <div className="position-relative">
                    <Menubar />
                    <div className="d-flex topbar align-items-center justify-content-between"> 
                        <div className="text-center text-white">
                            Отправка Новой почтой
                        </div>
                        <div className="text-center d-none d-lg-block text-white">
                            Товар всегда в наличии
                        </div>
                        <div className="text-center text-white">
                            +38 (099) 315-16-37
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Topbar;