import React from 'react';
import './composition.css';
import coconut from './coconut.png';
import coconutcomp from './coconutcomp.png';
import temp from './temp.png';
import microwave from './microwave.png';

const Composition = () => {
    return (
        <div className="text-center composition__bg my-5" id="section1"> 
            <h2 className="composition_heading">Общая информация</h2>
            <div className="row composition_content align-items-center">
                <div className="col-12 col-md-8 text-center mt-2">
                    Кокосовые пальмы растут главным образом в экваториальной зоне, т.е. в тропических регионах. 
                    Кокосовый орех является не к орехом, а односемянной костянкой. Он состоит из трёх сросшихся друг с другом плодолистиков, что зачастую приводит к их слегка трёугольной форме. Три глазка, которые видны на одной стороне, являются тремя отверстиями для прорастания, причём прорастает всегда только одно отверстие.
                </div>
                <div className="col-12 col-md-4">
                    <img src={coconut} className="img-fluid composition__main-image" />
                </div>
            </div>
            <h2 className="composition_heading mt-4">Масло состоит из</h2>
            <div className="row composition_content">
                <div className="col-12 col-sm-6 col-lg-4 text-left">
                    <div className="border rounded mt-2 p-2">
                        <h6><strong>Насыщенные жирные кислоты</strong></h6>
                        <div>
                            Лауриновая кислота (около 50%)
                            Миристиновая (20%) 
                            Пальмитиновая (9%)
                            Каприновая (5%)
                            Каприловая (5%)
                            Стеариновая (3%)
                            Гиалуроновая кислота
                            Арахидоновая кислота
                        </div>
                    </div>
                    <div className="border mt-2 rounded p-2">
                        <h6><strong>Витамины</strong></h6>
                        <div>
                            А, B1, B2, B3, С, Е, К
                        </div>
                    </div>
                </div>                
                <div className="col-12 col-sm-6 col-lg-4 text-left mt-2">
                    <div className="border rounded p-2">
                        <h6><strong>Полиненасыщенные жирные кислоты</strong></h6>
                        <div>
                            Линолевая кислота Омега-6
                            Линоленовая кислота Омега-3
                        </div>
                    </div>
                    <div className="border mt-2 rounded p-2">
                        <h6><strong>Галловая кислота</strong></h6>
                        <h6><strong>Эфиры жирных кислот и сложные эфиры полиолов</strong></h6>
                        <h6><strong>Производные жирных спиртов</strong></h6>
                    </div>                     
                </div>
                <div className="col-12 col-lg-4 text-left mt-2">
                    <div className="border rounded p-2">
                        <h6><strong>Мононенасыщенные жирные кислоты</strong></h6>
                        <div>
                            Олеиновая кислота
                        </div>
                    </div>
                    <div className="border mt-2 rounded p-2">
                        <h6><strong>Минералы</strong></h6>
                        <div>
                            железо, кальций, цинк, фосфор
                        </div> 
                    </div>
                </div>
            </div>
            <div className="row mt-4">
                <div className="col-12 col-lg-4 d-flex align-items-center">
                    <img src={temp} className="img-fluid composition_image" />
                    <div className="ml-3 mt-3 mt-sm-0 composition_content">
                        Застывает при температуре ниже 25% (меняет консистенцию из жидкой на более плотную)
                    </div>
                </div>
                <div className="col-12 col-lg-4 mt-2 mt-lg-0
                                d-flex align-items-center">
                    <img src={coconutcomp} className="img-fluid composition_image" />
                    <div className="ml-3 mt-3 mt-sm-0 composition_content">
                        Представляет собой бледно-желтую жидкость с ярко выраженным кокосовым запахом
                    </div>
                </div>
                <div className="col-12 col-lg-4 mt-2 mt-lg-0
                                d-flex align-items-center">
                    <img src={microwave} className="img-fluid composition_image" />
                    <div className="ml-3 mt-3 mt-sm-0 composition_content">
                        Внимание! Не нагревайте продукт в СВЧ-печи – от этого теряются все его полезные свойства
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Composition;