import axios from 'axios';

export const authenticationStart = () => {
    return {
        type: 'AUTH_START'
    };
};

export const authenticationSuccess = (token, userId) => {
    return {
        type: 'AUTH_SUCCESS',
        idToken: token,
        userId: userId
    };
};

export const authenticationFailure = (error) => {
    return {
        type: 'AUTH_FAILURE',
        error: error
    };
};

export const authProcess = (email, password) => {
    return dispatch => {
        dispatch(authenticationStart());
        const authData = {
            email: email,
            password: password,
            returnSecureToken: true
        };
        axios.post('https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyBE_eP4eLkhLBeuig8DnKX93z270uwDCpI', authData)
        .then(response => {
            console.log(response);
            dispatch(authenticationSuccess(response.data.idToken, response.data.localId));
        })
        .catch(error => console.log(error));
    };
};
