import React from 'react';
import './footer.css';
import logo from './logo_main.png';
import { Link } from 'react-router-dom';

const Footer = () => {
    return (
        <div className="bg-dark footer__bar mt-4">
            <div className="container">
                <div className="row">
                    <div className="col-12 footer__data d-flex justify-content-around">
                        <div>Состав</div>
                        <div>Применение</div>
                        <div>Каталог</div>
                    </div>
                    <div className="col-12 text-center">
                        <Link to="/orders">&copy; Amelis 2016-2020</Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Footer;