import React from 'react';
import './onestep.css';

const Onestep = (props) => {
    return (
        <div className="text-center p-3">
            <img src={props.image} className="img-fluid onestep__image" />
            <div className="mt-2">
                Добавьте товар в корзину
            </div>
        </div>
    );
}

export default Onestep;