import React from 'react';
import './onevariante.css';

const Onevariante = (props) => {
    return (
        <div className="align-items-center row">
            <div className="variante__image col-12 col-md-4 text-center">
                <img src={props.image} className="img-fluid border rounded" />
            </div>
            <p className="variante__data col-12 col-md-8 mt-2 mt-md-0">
                {props.data}
            </p>
        </div>
    );
}

export default Onevariante;