import React from 'react';
import './howtoproduce.css';
import Onestage from './onestage';
import step1 from './step1.jpg';
import step2 from './step2.jpg';
import step3 from './step3.jpg';
import step4 from './step4.jpg';

const Howtoproduce = () => {
    return (
        <div className="text-center">
            <h2 className="howtoproduce__heading">Его изготавливают методом холодного отжима </h2>
            <div className="row">
                <p className="col-12 howtoproduce__content">
                    Холодный отжим – наиболее щадящий процесс, он позволяет сохранять больше полезных веществ. 
                    Из тысячи зрелых кокосовых орехов весом примерно в полторы тонны выходит около 170 кг мякоти, 
                    а из нее холодным отжимом добывается всего 70 л кокосового масла высшего качества.
                </p>
            </div>
            <div className="row mt-2 howtoproduce__content">
                <div className="col-12 col-sm-6 col-md-3 mt-2 text-center">
                    <Onestage image={step1} />
                </div>
                <div className="col-12 col-sm-6 col-md-3 mt-2 text-center">
                    <Onestage image={step2} />
                </div>
                <div className="col-12 col-sm-6 col-md-3 mt-2 text-center">
                    <Onestage image={step3} />
                </div>
                <div className="col-12 col-sm-6 col-md-3 mt-2 text-center">
                    <Onestage image={step4} />    
                </div>
            </div>
        </div>
    );
}

export default Howtoproduce;