import React from 'react';
import './properties.css';
import Property from './property';

const Properties = () => {

    const dataPropertiesOne = ["Метаболизируясь особым методом, жирные кислоты оказывают положительное воздействие на головной мозг",
                            "Вещества, содержащиеся в масле, оказывают прекрасный антиоксидантный эффект, что уменьшает вероятность развития болезней сосудов и сердца", 
                            "Поможет снизить повышенную вязкость крови и укрепить стенки артерий"];
    const dataPropertiesTwo = [                        
                            "Масло кокоса способно повышать иммунитет, а при регулярном потреблении существенно уменьшать вероятность возникновения раковых болезней и восстанавливать функции щитовидной железы",
                            "Снимает стресс и психическую усталость, оказывает седативное действие",
                            "Кокосовое масло способно помогать в лечении ряда кожных заболеваний. Оно обладает противогрибковыми, противовирусными, антибактериальными и противовоспалительными свойствами"];
    const propertiesListOne = dataPropertiesOne.map((item)=> 
        <div className="col-12 col-lg-4 mt-3 mt-lg-0">
            <Property data={item} />
        </div>
    );
    const propertiesListTwo = dataPropertiesTwo.map((item)=> 
        <div className="col-12 col-lg-4 mt-3 mt-lg-0">
            <Property data={item} />
        </div>
    );

    return (
        <div>
            <h2 className="property__heading text-center">Полезные свойства</h2>
            <div className="row property__content mt-4">
                {propertiesListOne}
            </div>
            <div className="row property__content mt-4">
                {propertiesListTwo}
            </div>
        </div>
    );
}

export default Properties;