import React, { Component } from 'react';
import './cataloge.css';
import Product from './product';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as cartActions from '../../actions/cart';
import productImage200 from './kokos_200.jpg';
import productImage100 from './kokos_100.jpg';
import productImage1000 from './kokos_1000.jpg';

class Cataloge extends Component {
    state = {
        products: [
            {   id: 1,
                title: 'Кокосовое масло',
                value: 200,
                price: 130,
                image: productImage200
            },
            {   id: 2,
                title: 'Кокосовое масло',
                value: 110,
                price: 65,
                image: productImage100
            },
            {   id: 3,
                title: 'Кокосовое масло',
                value: 1000,
                price: 550,
                image: productImage1000
            }
        ]
    };
    render() {
        const { products } = this.state;
        console.log(products);
        return (
            <div className="text-center my-5" id="section2"> 
                <h2 className="cataloge__heading">Каталог</h2>
                <div className="row justify-content-between">
                    { products.map((product) => <Product {...product} key={product.id}/> ) }   
                </div>
            </div>
        );
    }
}

const mapStateToProps = ({cart}) => ({
    totalPrice: cart.cartItems,
    count: cart.orderTotal
});

const mapDispatchToProps = dispatch => ({
...bindActionCreators(cartActions, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(Cataloge);