import React from 'react';
import './slider.css';
import slider from './100_200.jpg';
import body from './body.jpeg';
import face from './face.png';
import hair from './hair.png';
import nails from './nails2.png';

const Slider = () => {
    return (
        <div>
            <div className="row slider align-items-center">
                <h1 className="slider__text col-12 col-md-6 text-center text-md-left">
                    Натуральное кокосовое масло холодного отжима
                </h1>
                <div className="slider__image-holder my-2 col-12 col-md-6">
                    <img src= { slider } className="img-fluid slider__image" />
                </div>
            </div>
            <div className="row my-3 slider__purpose">
                <div className="image col-6 col-md-3 d-flex p-2 align-items-center">
                    <img src={body} className="img-fluid slider__purpose-image" />
                    <div className="ml-3 for__text">для тела</div>
                </div>
                <div className="image col-6 col-md-3 d-flex p-2 align-items-center">
                    <img src={hair} className="img-fluid slider__purpose-image" />
                    <div className="ml-3 for__text">для волос</div>
                </div>
                <div className="image col-6 col-md-3 d-flex p-2 align-items-center">
                    <img src={face} className="img-fluid slider__purpose-image" />
                    <div className="ml-3 for__text">для лица</div>
                </div>
                <div className="image col-6 col-md-3 d-flex p-2 align-items-center">
                    <img src={nails} className="img-fluid slider__purpose-image" />
                    <div className="ml-3 for__text">для ногтей</div>
                </div>
            </div>
        </div>
    );
}

export default Slider;