import React from 'react';
import './thankyoupage.css';
import Header from '../header';
import { connect } from 'react-redux';

const Thankyoupage = ({isMenuShow}) => {
    return (
        <div className="container my-5">
            <Header isMenuShow={!isMenuShow}/>
            <div className = "d-flex flex-column align-items-center">
                <h2>Ваш заказ отправлен</h2>
                <div>
                    <img src="http://placehold.it/150x150/" className="img-fluid" />
                </div>
                <div>
                    Спасибо, мы свяжемся с вам в течении 1-2 часов
                </div>
            </div>
        </div>
    );
}

const mapStateToProps = ({menu}) => ({
    isMenuShow: menu.isMenuShow
});

export default connect(mapStateToProps)(Thankyoupage);