import React from 'react';

const Order = (props) => {
    const {customer, products, price, count} = props;
    const { branch, city, 
            orderNumber, orderDate, 
            email, phone, 
            name, fullname, paymentType} = customer;
    
    const orderedProducts = products.map((item) => {
        return (
            <div className="border p-2 my-1 bg-light">
                <div>Товар {item.title} {item.value}</div>
                <div>Количество {item.count}</div>
            </div>
        )
    });

    return (
        <div className="col-12 col-md-6 col-lg-4 p-3 border rounded">
                <h3 className="text-center">Заказ № {orderNumber}</h3>
                <div><b>Общая стоимость</b> - {price}</div>
                <div><b>Количество товаров</b> - {count}</div>
                <div><b>Отделение НП</b> - {branch}</div>
                <div><b>Город</b> - {city}</div>
                <div><b>Email</b> - {email}</div>
                <div><b>Телефон</b> {phone}</div>
                <div><b>Имя</b> - {name}</div>
                <div><b>Фамилия</b> - {fullname}</div>
                <div><b>Способ оплаты</b> - {paymentType}</div>
                <div><b>Время заказа</b> - {orderDate}</div>
                <h4 className="text-center">Заказанные товары</h4>
                { orderedProducts }
        </div>
    );
};

export default Order;