import React from 'react';
import './offer.css';
import offer from './offer.jpg';

const Offer = () => {
    return (
        <div className="my-5">
            <div className="offer__content row align-items-center 
                            rounded border border-dark p-1">
                <div className="col-4 text-center">
                    <img src={offer} className="img-fluid" />
                </div>
                <div className="col-8">
                    <h2 className="offer__heading text-left">Акция!</h2>
                    <p>Все заказы, сумма которых превышает 500 гривен, 
                        мы доставляем бесплатно на любое отделение Новой Почты.
                    </p>
                </div>
            </div>
        </div>
    );
}

export default Offer;