import React, { Component } from 'react';
import './checkout.css';
import axios from 'axios';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import * as cartActions from '../../actions/cart';
import Header from '../header';
import { Formik } from 'formik';
import * as Yup from 'yup';
import Error from './error';

class Checkout extends Component {
    state = {
        isClicked: false, 
        isOk: false
    }

    checkingStatus = (response) => {
        this.setState({isOk: true});
        console.log(response);
    }

    buttonHandler = (orderData) => {
        console.log(`order`, orderData);
        this.setState({isClicked: true});
        const { totalItems, totalPrice, clearBasket, orderedItems } = this.props;
        clearBasket();
        
        let orderedProduct = orderedItems.map((item) => {
            const {id, title, value, price, count, total} = item;
            return ({ id, title, value, price, count, total });
        });
       
        let orderDate = new Date().toLocaleTimeString();
        let orderNumber = Math.floor(Math.random() * 10001);
        const order = {
             price: {totalItems},
             totalNumber: {totalPrice},
             orderedProduct,
             customer: {
                 name: orderData.name,
                 fullname: orderData.fullname,
                 email: orderData.email,
                 phone: orderData.phone,
                 city: orderData.city,
                 branch: orderData.branch,
                 orderDate: orderDate,
                 orderNumber: orderNumber,
                 paymentType: orderData.paymentOptions
             }
         }
         axios.post('https://coconut-oil-landingpage.firebaseio.com/orders.json', order)
         .then((response) => this.checkingStatus(response))
         .catch(error => console.log(error));
    }
    
    /*
    inputHandlerName = (event) => {
        const updatedInput = {
            ...this.state
        };
        updatedInput.inputName = event.target.value;
        this.setState({inputName: updatedInput.inputName});
    }
    */
    render () {
        const { totalItems, totalPrice, orderedItems, isMenuShow } = this.props;
        let order = orderedItems.map((item) => {
            const {id, title, value, price, count, total} = item;
            return (
                <div className="pt-1">
                   Масло {value}мл {count}шт. -{total} грн.
                </div>
            );
        });

        if (this.state.isOk) {
            return < Redirect to="/thankyoupage"/>
        }

        const validationSchema = Yup.object().shape({
            name: Yup.string()
                .min(2, "Must have at least 2 characters")
                .max(30, "Must be shorter than 30")
                .required("Must enter a name"),
            fullname: Yup.string()
                .min(2, "Must have at least 2 characters")
                .max(30, "Must be shorter than 30")
                .required("Must enter a fullname"),
            city: Yup.string()
                .min(2, "Must have at least 2 characters")
                .max(30, "Must be shorter than 30")
                .required("Must enter a city"),  
            branch: Yup.string()
                .min(2, "Must have at least 2 characters")
                .max(30, "Must be shorter than 30")
                .required("Must enter a branch"),
            phone: Yup.string()
                .min(2, "Must have at least 2 characters")
                .max(30, "Must be shorter than 30")
                .required("Must enter a phone"),           
            email: Yup.string()
                .email("Must be a valid email address")
                .max(30, "Must be shorter than 30")
                .required("Must enter an email")            
        });
        
        return (
            <div className="container">
                <Header isMenuShow={!isMenuShow}/>
                <h3>Оформление заказа</h3>
                <div className="order-data d-flex my-2 justify-content-around">
                    <div className="w-50 border rounded p-2">
                        { order }
                    </div>
                    <div className="w-50 border rounded p-2">
                        <div>
                            Общее количество: ({ totalPrice })
                        </div>
                        <div className="mt-3">
                            Cумма заказа: { totalItems } грн
                        </div>
                    </div>
                </div>
                <div className="row border bg-light rounded">
                    <Formik 
                        initialValues={{name: "", email: "", fullname: "",
                                        phone: "", city: "", branch: "", paymentOptions: ""}}
                        validationSchema={validationSchema}
                        onSubmit = {(values, {setSubmitting, resetForm})=>{
                            this.buttonHandler(values);
                            resetForm();
                        }}
                    >

                        {({
                            values, 
                            errors, 
                            touched, 
                            handleChange, 
                            handleBlur, 
                            handleSubmit, 
                            isSubmitting,
                            setFieldValue
                        })=>(
                            

                            <form onSubmit={handleSubmit} className="order__fields col-12">
                                <div className="form-row">
                                    <div className="form-group col-12 col-sm-7 col-md-6">
                                        <label htmlFor="name">Имя</label>
                                        <input  
                                            onChange = {handleChange} 
                                            onBlur = {handleBlur}
                                            value = {values.name}
                                            type="text" 
                                            className="form-control"
                                            name="name" 
                                            id="name"
                                        />
                                        <Error touched={touched.name} message={errors.name}/>
                                    </div>
                                    <div className="form-group col-12 col-sm-7 col-md-6">
                                        <label htmlFor="fullname">Фамилия</label>
                                        <input  
                                            onChange={ handleChange}
                                            onBlur = {handleBlur}
                                            value = {values.fullname} 
                                            type="text" 
                                            className="form-control"
                                            name="fullname" 
                                            id="fullname"
                                        />
                                        <Error touched={touched.fullname} message={errors.fullname}/>
                                    </div>
                                </div>
                                <div className="form-row">
                                    <div className="form-group col-12 col-sm-7 col-md-6">
                                        <label htmlFor="email">Email</label>
                                        <input  
                                            onChange={ handleChange } 
                                            onBlur = {handleBlur}
                                            value = {values.email}
                                            type="email" 
                                            className="form-control"
                                            name="email" 
                                            id="email"
                                        />
                                        <Error touched={touched.email} message={errors.email}/>
                                    </div>
                                    <div className="form-group col-12 col-sm-7 col-md-6">
                                        <label htmlFor="phone">Телефон</label>
                                        <input  
                                            onChange={ handleChange }
                                            onBlur = {handleBlur}
                                            value = {values.phone} 
                                            type="text" 
                                            className="form-control"
                                            name="phone"  
                                            id="phone"
                                        />
                                        <Error touched={touched.email} message={errors.email}/>
                                    </div>
                                </div>
                                <div className="form-row">
                                    <div className="form-group col-12 col-sm-7 col-md-6">
                                        <label htmlFor="city">Город</label>
                                        <input 
                                            onChange={ handleChange }
                                            onBlur = {handleBlur}
                                            value = {values.city} 
                                            type="text" 
                                            className="form-control"
                                            name="city"   
                                            id="city"
                                        />
                                        <Error touched={touched.city} message={errors.city}/>
                                    </div>
                                    <div className="form-group col-12 col-sm-7 col-md-6">
                                        <label htmlFor="branch">Отделение Новой Почты</label>
                                        <input
                                            onChange={ handleChange }
                                            onBlur = {handleBlur}
                                            value = {values.branch} 
                                            type="text" 
                                            className="form-control"
                                            name="branch"   
                                            id="branch"
                                        />
                                        <Error touched={touched.branch} message={errors.branch}/>
                                    </div>
                                </div>
                                <div className="form-check form-check-inline my-2">
                                    <input
                                        onChange={() => setFieldValue("paymentOptions", "cardType")}
                                        onBlur = {handleBlur}
                                        value = {values.paymentOptions}  
                                        type="radio" 
                                        name="paymentOptions" 
                                        id="cardType"
                                        checked={values.paymentOptions === "cardType"}/>
                                    <label for="cardType">Оплата на карту</label>
                                
                                    <input
                                        onChange={() => setFieldValue("paymentOptions", "cashType")}
                                        onBlur = {handleBlur}
                                        value = {values.paymentOptions}  
                                        className="ml-5 " 
                                        type="radio" 
                                        name="paymentOptions"
                                        checked={values.paymentOptions === "cashType"} 
                                        id="cashType"/>
                                    <label for="cashType">Оплата наложенным платежом</label>
                                </div>
                                <div className="text-center my-4">
                                    <button disabled = {isSubmitting} className="btn btn-lg btn-danger" type="submit">
                                        Подтвердить заказ
                                    </button>
                                </div>
                            </form>
                            )
                        }
                    </Formik>
                    
                </div>
            </div>
        );
    }
}

const mapStateToProps = ( {cart, menu} ) => ({
    totalPrice: cart.cartItems.reduce((total, book) => total + book.count, 0),
    orderedItems: cart.cartItems,
    totalItems: cart.orderTotal,
    isMenuShow: menu.isMenuShow
});

const mapDispatchToProps = dispatch => ({
    ...bindActionCreators(cartActions, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(Checkout);