export const onAddToCart = obj => ({
    type: 'ON_ADD_BOOK',
    payload: obj
});

export const onDecrease = obj => ({
    type: 'ON_DECREASE',
    payload: obj
});

export const onDelete = obj => ({
    type: 'REMOVE_BOOK',
    payload: obj
});

export const clearBasket = () => ({
    type: 'CLEAR_BASKET'
});

export const onCloseModal = () => ({
    type: 'ON_CLOSE_MODAL'
});

