const initialState = {
    token: null,
    userId: null,
    loading: false,
    error: null
};

export default (state = initialState, action) => {
    switch (action.type) {
        case 'AUTH_START':
            return {
                ...state,
                loading: true
                
            };
        case 'AUTH_SUCCESS':
            return {
                ...state,
                token: action.idToken,
                userId: action.userId
            };
        case 'AUTH_FAILURE':
            return {
                ...state,
                error: action.error
            };
        default:
            return state;
    }
};

