import React, { Component } from 'react';
import './header.css';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import logo from './logo_main.png';
import { bindActionCreators } from 'redux';
import * as menuActions from '../../actions/menu';


class Header extends Component {
    render() {
        const { totalItems, totalPrice, onMenuOpen, isMenuShow} = this.props;
        return (
            <div>
                <div className="border-bottom">
                    <div className="align-items-center row justify-content-between no-gutters">
                        <div className="col-3 col-md-3">
                            {isMenuShow ? <i onClick={ () => onMenuOpen() } className="fa fa-bars fa-3x" aria-hidden="true"></i>
                            : <button className="btn btn-sm btn-outline-secondary"> <Link to="/">на главную</Link></button> }
                        </div>
                        <div className="header__logo col-6 col-md-3">
                            <Link to="/">
                                <img src={logo} className="img-fluid" />
                            </Link>
                        </div>
                        {isMenuShow ? <div className="col-3 col-md-3 d-flex justify-content-end align-items-center">
                            {/*<div className="cart__icon">
                                <i className="fa fa-shopping-cart fa-2x" aria-hidden="true"></i>
                            </div>*/}
                            <div className="cart__data d-flex flex-column">
                                <div>
                                    <span>
                                        <Link to="/cart">
                                            <span className="text-dark">Корзина</span>
                                        </Link>
                                    </span>
                                    <span className="cart__amount ml-1">
                                        ({totalPrice})
                                    </span>
                                </div>
                                <div className="text-right">
                                    {totalItems} &#8372;
                                </div>
                            </div>
                        </div> : <div className="col-4"></div>} 
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = ({ cart, menu }) => ({
    totalPrice: cart.cartItems.reduce((total, book) => total + book.count, 0),
    orderedItems: cart.cartItems,
    totalItems: cart.orderTotal
});

const mapDispatchToProps = dispatch => ({
    ...bindActionCreators(menuActions, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(Header);